import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { CircularProgressbarWithChildren  } from 'react-circular-progressbar';
import 'react-circular-progressbar/dist/styles.css';
import Grid from '@material-ui/core/Grid';


export default class Skills extends Component {

  card = s => (
    <Grid className="content-item" key={s.name} item xs={6} sm={4} md={2}>
      <CircularProgressbarWithChildren 
        value={s.amount}
        strokeWidth={3}
        background
        counterClockwise
        backgroundPadding={4}
        styles={{
          background: {
            fill: '#ededed',
          },
        }}
      >
        <div className="skill-text">{s.name}</div>
      </CircularProgressbarWithChildren>
    </Grid>
  );

  type = (t, ss) => (
    <div key={t}>
      <h2 className="info-flex info-title">
        <i style={{color: "goldenrod"}} className="fa fa-spinner" />
        <div>{String(t[0]).toUpperCase() + Array.from(t.replace(/(\w+)(\d+)/, "$1 $2")).filter((c, k) => k!==0?c:"").join("")}</div>
      </h2>
      <Grid container justify="center" className="info-content" spacing={16}>
        {
          ss.map(s => this.card(s))
        }
      </Grid>
    </div>
  );

  render() {
    const {data} = this.props;
    return (
      <div id="skills">
        <h1 className="title">Skills</h1>
        <div className="info">
          {
            Object.keys(data).map(e => this.type(e, data[e]))
          }
        </div>
      </div>
    );
  }
}


Skills.propTypes = {
  data: PropTypes.shape(),
};
Skills.defaultProps = {
  data: {},
};
