import React, { Component } from 'react';
import PropTypes from 'prop-types';

export default class Experiments extends Component {

  card = e => (
    <div key={e.name}>
      <h2 className="info-flex info-title">
        <i className="fa fa-briefcase" />
        <div>{e.role}</div>
      </h2>
      <div className="info-content">
        <div className="info-flex info-date">
          <i className="far fa-clock" />
          <div>{e.date}</div>
        </div>
        <div className="info-flex info-date">
          <i className="far fa-compass" />
          <div>{e.location}</div>
        </div>
        <div className="url"><div className="img"><img src={e.logo} alt={e.name} /></div><a href={e.url}>{e.name}</a></div>
        <div className="info-desc">
          {
            e.description && 
            e.description.map(d => (
              <div key={d}>{d}</div>
            ))
          }
        </div>
      </div>
    </div>
  )


  render() {
    const {data} = this.props;
    return (
      <div className="work" id="experiments">
        <h1 className="title">Experience</h1>
        <div className="info">
          {
            data.map(e => this.card(e))
          }
        </div>
      </div>
    );
  }
}

Experiments.propTypes = {
  data: PropTypes.arrayOf(PropTypes.shape()),
};
Experiments.defaultProps = {
  data: [],
};