import React, { Component } from 'react';
import PropTypes from 'prop-types';

import Grid from '@material-ui/core/Grid';

export default class Title extends Component {
  render() {
    const {data} = this.props;
    
    return (
      <div id="title">
        <div className="title">
          {
            data.avatar &&
            (
              <>
                <div className="img"><img src={data.avatar} alt="Alireza Kavian" /></div>
                <div className="info">
                  <h1 className="info-title">{data.title}</h1>
                  <h2 className="info-subtitle">{data.subtitle}</h2>
                  <div className="info-desc">
                    {
                      data.description.map(el => <div key={el}>{el}</div>)
                    }
                  </div>
                  <Grid container justify="flex-start" className="info-social" spacing={0}>
                    {
                      data.social.map(el => 
                        (
                          <Grid key={el.url} item xs={3} sm={3} md={1}>
                            <a key={el.icon} href={el.url} target="_blank" rel="noopener noreferrer"><i className={el.icon} /></a>
                          </Grid>
                        )
                      )
                    }
                  </Grid>
                </div>
              </>
            )
          }
        </div>
      </div>
    );
  }
}

Title.propTypes = {
  data: PropTypes.shape(),
};
Title.defaultProps = {
  data: {},
};