import React, { Component } from 'react';

import './assets/css/default.min.css';

import Title from './components/Title';
import About from './components/About';
import Education from './components/Education';
import Experiments from './components/Experiments';
import Projects from './components/Projects';
import Skills from './components/Skills';

class App extends Component {
  constructor() {
    super();
    this.state = {
      data: {}
    };
    this.getData();
  }
  getData() {
    fetch("/assets/data.json", { method: "GET" })
      .then(json => json.json())
      .then((data) => {
        this.setState({ data });
      })
      .catch(err => console.error(err));
  }

  footer = () => (
    <footer id="footer">© 2017-2019 <a href="https://alireza-kavian.github.io">Alireza Kavian</a> . All rights reserved.</footer>
  );

  render() {
    const { data } = this.state;
    return (
      <div className="App">
        <Title data={data.header} />
        <About data={data.about} />
        <Education data={data.resume ? data.resume.education : []} />
        <Experiments data={data.resume ? data.resume.experience : []} />
        <Projects data={data.resume ? data.resume.projects : {}} />
        <Skills data={data.resume ? data.resume.skills : {}} />
        {this.footer()}
      </div>
    );
  }
}

export default App;
