import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Button from '@material-ui/core/Button';

export default class About extends Component {
  render() {
    const { data } = this.props;
    return (
      <div id="about">

        {
          data.contact &&
          (
            <>
              <div className="download"><a href="https://alirezakay.github.io/Docs/Alireza-Kavian-Resume.pdf"><Button variant="outlined">Download Resume</Button></a></div>
              <div className="info">
                <div className="about">
                  <div>About</div>
                  <div>{data.description.map(el => <div key={el}>{el}</div>)}</div>
                </div>
                <div className="age">Age: {(new Date().getFullYear()) - 1997}</div>
                <div className="address">
                  <div>Address</div>
                  <div>{data.contact.map(el => <div key={el}>{el}</div>)}</div>
                </div>
              </div>
            </>
          )
        }
      </div>
    );
  }
}

About.propTypes = {
  data: PropTypes.shape(),
};
About.defaultProps = {
  data: {},
};
