import React, { Component } from 'react';
import PropTypes from 'prop-types';

export default class Education extends Component {

  card = e => (
    <div key={e.name}>
      <h2 className="info-flex info-title">
        <i className="fa fa-graduation-cap" />
        <div>{e.name}</div>
      </h2>
      <div className="info-content">
        <div className="info-flex info-date">
          <i className="far fa-clock" />
          <div>{e.date}</div>
        </div>
        <div className="url"><div className="img"><img src={e.logo} alt={e.name} /></div><a href={e.url}>Link</a></div>
        <div className="info-degree">{e.degree}</div>
        <div className="info-gpa">GPA: {e.gpa}</div>
      </div>
    </div>
  )


  render() {
    const {data} = this.props;
    return (
      <div className="work" id="education">
        <h1 className="title">Education</h1>
        <div className="info">
          {
            data.map(e => this.card(e))
          }
        </div>
      </div>
    );
  }
}

Education.propTypes = {
  data: PropTypes.arrayOf(PropTypes.shape()),
};
Education.defaultProps = {
  data: [],
};