import React, { Component } from 'react';
import PropTypes from 'prop-types';

export default class Projects extends Component {

  card = w => (
    <div key={w.name}>
      <div className="content-top info-flex">
        <i className={w.icon} />
        <div>{w.for}</div>
      </div>
      <div className="content-bottom">
        <div>{w.name}</div>
        <a href={w.url} target="_blank" rel="noopener noreferrer"><i className="fa fa-external-link-alt" /></a>
      </div>
    </div>
  );

  year = (y, d) => (
    <div key={y}>
      <h2 className="info-flex info-title">
        <i style={{color: d.color}} className="fa fa-calendar-alt" />
        <div>{String(y[0]).toUpperCase() + Array.from(y.replace(/(\w+)(\d+)/, "$1 $2")).filter((c, k) => k!==0?c:"").join("")}</div>
      </h2>
      <div className="info-flex info-date">
        <i className="far fa-clock" />
        <div>{d.date}</div>
      </div>
      <div className="info-content">
        {
          d.work && d.work.map(w => this.card(w))
        }
      </div>
    </div>
  );


  render() {
    const {data} = this.props;    
    return (
      <div id="projects">
        <h1 className="title">Projects</h1>
        <div className="info">
          {
            Object.keys(data).reverse().map(e => this.year(e, data[e]))
          }
        </div>
      </div>
    );
  }
}

Projects.propTypes = {
  data: PropTypes.shape(),
};
Projects.defaultProps = {
  data: {},
};